// Google API accepts only 5 country codes
export const countriesWithMultipleCCA3: Partial<Record<CountryCCA3, CountryCCA3[]>> = {
  FRA: ["FRA" as const, "GUF" as const, "REU" as const, "MTQ" as const, "GLP" as const], // France, French Guiana, R√©union, Martinique, Guadeloupe
};

export const isCountryCCA3 = (value: unknown): value is CountryCCA3 =>
  countries.some(({ cca3 }) => cca3 === value);

export const isCountryCCA2 = (value: unknown): value is CountryCCA2 =>
  countries.some(({ cca2 }) => cca2 === value);

export const isIndividualCountryCCA3 = (value: unknown): value is IndividualCountryCCA3 =>
  individualCountries.includes(value as IndividualCountryCCA3);

export const isCompanyCountryCCA3 = (value: unknown): value is CompanyCountryCCA3 =>
  companyCountries.includes(value as CompanyCountryCCA3);

export const isCompanyWithUboCountryCCA3 = (value: unknown): value is CompanyWithUboCountryCCA3 =>
  companyWithUboCountries.includes(value as CompanyWithUboCountryCCA3);

const getBestNavigatorCountryIndividual = (): IndividualCountryCCA3 => {
  const navigatorCountries = navigator.languages
    .map(lang => lang.split("-")[1])
    .filter(isNotNullish);

  const cca2 = navigatorCountries.find(isCountryCCA2) ?? "FR";
  const country = individualCountries.find(country => getCCA2forCCA3(country) === cca2);

  return country ?? "FRA";
};

const getBestNavigatorCountryCompany = (): CompanyCountryCCA3 => {
  const navigatorCountries = navigator.languages.map(lang => lang.split("-")[1]);
  const cca2 = navigatorCountries.find(isCountryCCA2) ?? "FR";
  const country = companyCountries.find(country => getCCA2forCCA3(country) === cca2);

  return country ?? "FRA";
};

export const individualFallbackCountry = getBestNavigatorCountryIndividual();
export const companyFallbackCountry = getBestNavigatorCountryCompany();

const hasIntl = "Intl" in window && "DisplayNames" in window.Intl;
const countryNameResolver =
  hasIntl && Intl.DisplayNames.supportedLocalesOf([locale.language]).length
    ? new Intl.DisplayNames([locale.language], { type: "region" })
    : undefined;

export const getCountryName = (cca3: CountryCCA3): string => {
  const cca2 = getCCA2forCCA3(cca3);

  if (isNullish(cca2)) {
    if (isTranslatedCountry(cca3)) {
      return names[cca3];
    }
    return countries.find(country => country.cca3 === cca3)?.name ?? cca3;
  }

  const resolvedName = countryNameResolver?.of(cca2);

  if (isNotNullish(resolvedName)) {
    return resolvedName;
  }

  if (isTranslatedCountry(cca3)) {
    return names[cca3];
  }
  return countries.find(country => country.cca3 === cca3)?.name ?? cca3;
};
